iframe{
  width: 250px;
}
h3{
  text-align: center;
  text-decoration: underline;
  padding-bottom: 1em;
  padding-top: 1em;
}
ul{
  list-style-type: none;
}
li{
  text-align: left;
  padding: 0.5em;
}
p{
  text-align: center;
  padding-bottom: 0.5em;
}
.blackWhite{
  color: lightgray
}
.note_stripes{
  padding-top: 1em;
}
.container_form{
  font-family: Ariel;
  padding: 1em;
  background-image: linear-gradient(darkGray, lightblue, lightblue, lightblue, orange);
  opacity: 0.8;
  border-color: darkGray;
  border-style: double;
}
.label_name{
  padding: 1em;
  margin-top: 8em;
}
.label_colour{
  padding: 1em;
  margin-left: 3%;
  margin-top: 8em;
}
.input_name{
  margin-left: 1%;
  padding: 0.5em;
  width: 20%;
  font-size: 0.8em;
  margin-right: 1%;
  margin-top: 8em;
}
.input_colour{
  padding: 0.5em;
  margin-left: 1%;
  margin-bottom: 1em;
  font-size: 0.8em;
  margin-top: 8em;
}
.submit{
  padding: 0.5em;
  border-style: inset;
  border-radius: 0.5em;
  font-size: 0.75em;
  margin: 0.5em;
  margin-left: 2%;
}
.payment_message{
  padding-top: 1em;
  margin-top: 3em;
  background-color: lightGray;
  border-style: ridge;
  text-align: center;
  opacity: 90%;
}
.attribution{ 
  font-size: 0.5em;
}

