  /* For mobile phones: */
*{
  margin: 0;
  padding: 0;
}
.bg{
  background: url("../images/SteppingStones.jpg")
  /*background-image: url("images/SteppingStones.jpg");*/
  /*margin-top: 50px;*/
}
nav{
width: 100%;
  background-image: linear-gradient(to right, lightblue, blue);
  position: fixed;
  top: 0;
  overflow: hidden;
  border-top: 10px solid white;
  z-index: 50;
}
nav li{
  display: block;
  list-style-type: none;
  cursor: pointer;
  padding: 10px;
  float: right;
  font-weight: bold;
  font-size: 19px;
  font-family: Monospace, "Courier New";
  font: black;
}
a:hover{
  color: lightYellow;
}
a:link {
  color: darkBlue;
  text-decoration: underline;
}
nav li:visited{
  color: purple;
}

h1{
  text-align: center;
  padding-top: 1em;
  font-family: 'Shojumaru', cursive;
  font-size: 2em;
}
.logo{
  padding-top: 4em;
  display: block;
  margin-left: auto;
  margin-right: auto;  width: 50%;
  height: auto;
  text-align: center; /*change to align with different @media*/
}
h3{
  padding-bottom: 1em;
  padding-top: 1em;
  text-align: center;
  text-decoration: underline;
  font-size: 1.5em;
}
p{
  font-size: 20px;
  text-align: center; 
  font-weight: medium;
  font: darkgreen;
  padding: 0.2em;
}
.container_contact{
  background-color: lightgreen;
  /*margin: 15px 0px;*/
  opacity: 0.8;
  text-align: center;
  font-size: 1.2em;
}
.email{
  font-size: 1em;
}
.newStyle{
  padding-left: 1.5em;
  font-family: "Andale Mono", Monospace, "Courier New";
  color: blue;
  font-size: 20px;
  text-shadow: 2px 2px gray;
  font-weight: bold;
}
.welcome{
  font-weight: bold;
  font-size: 23px;
  font-family: Monospace, "Courier New";
  padding: 10px;
  text-align: center;
}
img{
  width: 90%;
  padding: 0px 5px;  
  margin-left: auto;
  margin-right: auto;
}
.photos{  
 /*flex box in container*/
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}
.container_about{
  background-color: lightGreen;
  margin-bottom: 1em;
  padding: 1em;
  font: darkGreen;
  opacity: 0.8;
}
  .container_calendar{
